<template>
	<div id="home" @click='closeHeaderPop'>
														<!-- @childByValue="childByValue" -->
		<we-header title='微信' ref='weHeader' :menuIndex.sync='menuIndex' :menuIsTop.sync='menuIsTop'></we-header>
		<ul v-if='chatList' class='chatList'  >
			<!-- menuIndex && e.preventDefault() ||   $refs.weHeader.isShow && e.preventDefault() -->
			<li v-for='(val,i) in chatList' @click='goChat($event,val,i)' @touchstart='touchStart($event,val,i)' @touchend='touchEnd' @touchmove='touchMove' class='chat' :class='menuIndex===i?"is_bgColor":""' >
				<img :src="val.avator" alt="">
				<div class ='desc' onselectstart = "return false;">	
					<p class="name" v-show='val.nickName'  >{{val.nickName}}</p>
					<p class="name" v-show='!val.nickName'  >{{val.name}}</p>
					<p class="title"  >{{val.say}}</p>
					<span class="time"  >{{val.maybeTime}}</span>
				</div>
				<ul class='listMenu' :class='[menuIndex===i?"is_show":"",menuIsTop===i?"menuIsTop":""]'>
					<li>标为未读</li>
					<li>置顶聊天</li>
					<li @click='isdiaLog=true'>删除该聊天</li>
				</ul>
			</li>
		</ul>
		<van-dialog
		  v-model="isdiaLog"
		  message="删除后，将清空该聊天的消息记录"
		  show-cancel-button
		  :close-on-click-overlay='true'
		  @confirm=confirmclear
		>
		</van-dialog>
	</div>
</template>
<script>
	import weHeader from '@/components/weHeader'
	import test from '@/components/test'
	import chatJosn from '@/assets/json/chatList.json'
	export default{
		name:'home',
		data(){
			return{
				homeTop:0,
				chatList:'',
				timeOutEvent:null,
				menuIndex:null,
				menuIsTop:null,
				isdiaLog:false,
			}		
		},
		methods:{
			goChat(e,val,cid){
				if(this.$refs.weHeader.isShow){
					return false;
				}
				else if(this.menuIndex!==null){
					this.menuIndex=null;
					return;
				}
				else{
					sessionStorage.setItem('nowChatUser',val.name)
					sessionStorage.setItem('nowCuserSay',val.say)
					sessionStorage.setItem('nowUserAvator',val.avator)
					if(val.nickName){
						sessionStorage.setItem('nowChatNickname',val.nickName)
					}
					else{
						sessionStorage.setItem('nowChatNickname','')
					}
					sessionStorage.setItem('nowChatNum',val.wechatNum)
					sessionStorage.setItem('nowUserSex',val.sex)
					sessionStorage.setItem('nowUserArea',val.area)
					sessionStorage.setItem('nowChatTime',val.time)
					this.$router.push('/chat')
				}		
			},
			closeHeaderPop(){
				this.$refs.weHeader.isShow=false;
				this.menuIndex=null;
				this.menuIsTop=null;

			},
			prevent(e){
				e.preventDefault();
			},
			touchStart(e,item,i){	
				sessionStorage.setItem('chatIndex',i);
				var winScrollTop=document.documentElement.scrollTop|| document.body.scrollTop;
				var thisScrollTop=e.currentTarget.offsetTop;
				// console.log(winScrollTop)
				// console.log(thisScrollTop);
				var isTop=thisScrollTop-winScrollTop;
				// console.log(isTop);
				this.timeOutEvent = setTimeout(()=>{  
					if(this.$refs.weHeader.isShow){
						this.closeHeaderPop();
						return;
					} 
					if(this.menuIndex!==null){
						this.menuIndex=null;
					    return;
					} 
	                this.timeOutEvent = 0;    
	                this.menuIndex=i;
	                if(isTop>360){
	                	this.menuIsTop=i;
	                }
	                return false;
	            }, 250);
			},
			touchMove: function() {    
	            clearTimeout(this.timeOutEvent);    
	            this.timeOutEvent = 0; 
	            return false;       
	        },    
			touchEnd(){
				clearTimeout(this.timeOutEvent);    
	            // if (this.timeOutEvent != 0) {    
	                // 点击事件    
	                // alert('你点击了');    
	            // }    
	            return false;    
			},
			// childByValue(childValue){
		 //        this.menuIndex=childValue;
		 //        this.menuIsTop=childValue;
		 //    },
		    confirmclear(){
		    	let chatIndex=sessionStorage.getItem('chatIndex');
		    	this.chatList.splice(chatIndex,1);
		    	sessionStorage.removeItem('chatIndex');
		    }
		},
		mounted(){
			// this.$axios.get('https://api.myjson.com/bins/mgx2p').then(res=>{
				this.chatList=chatJosn.data;
				// this.chatList=res.data.data;
			// })
		},
		activated() {
			this.closeHeaderPop();
			document.documentElement.scrollTop=document.body.scrollTop=this.homeTop;

	    },
		beforeRouteLeave (to, from, next) {
			
			if(this.menuIndex || this.$refs.weHeader.isShow || this.menuIsTop){
				this.closeHeaderPop();
				setTimeout(()=>{
				 	document.documentElement.scrollTop=document.body.scrollTop=this.homeTop;
				 	next(false);
				})	

		    }
		    else if(this.isdiaLog){
		    	this.isdiaLog=false;			
				setTimeout(()=>{
				 	document.documentElement.scrollTop=document.body.scrollTop=this.homeTop;
				 	next(false);
				})	
		    }	
		    else{
		    	next();
		    }	
		   	this.homeTop=document.documentElement.scrollTop || document.body.scrollTop;
		},
		components:{
			weHeader,test
		}
	}
</script>
<!-- <style src='../assets/css/demo.css' scoped></style> -->
<style scoped>
	h1{
		font-size: .4rem;
	}
	.chatList{
		padding:.86rem 0;
		background: #fff;
		margin-bottom: .58rem;
	}
	.chatList>li{
		display: flex;
		padding-top: .18rem;
		padding-left: .3rem;

	}
	.name{
		font-size: .38rem;
		margin:0;
		font-weight: 500;
		width: 60%;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	    color:#000;
	}
	.desc{
		border-bottom:1px solid rgba(233,233,233,.6);
		width: 100%;
		position: relative;
		padding-top: .1rem;
		overflow: hidden;
	}
	.hiddenPlace{
		position:fixed;
		width:100%;
		height:100%;
		z-index:9999;
		background:rgba(255,255,255,0);
		overflow:hidden;
		top:0px;
		left:0px;
	}
	.chatList>li:first-child{
		padding-top: .27rem;
	}
	.chatList>li:last-child>.desc{
		border-bottom:1px solid transparent;
	}
	img{
		width: 1.1rem;
		height:  1.1rem;
		margin-right: .2rem;	
		border-radius: .16rem;
	}
	.title{
		font-size: .3rem;
	    margin-top: .06rem;
    	margin-bottom: .16rem;
	    color: #A4A4A4;
	    width: 82%;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	}
	.time{
		font-size: .28rem;    
		color: #A4A4A4;
		position: absolute;
		top:.1rem;
		right: .23rem;
	}
	.chat{
		position: relative;
	}
	.listMenu{
		position: absolute;
		right: 1rem;
		background: white;
		display: none;
		top: .82rem;
		z-index: 6868;
	}
	.is_show{
		display: block;
		box-shadow: 0.01rem 0.01rem 0.1rem 0.02rem #ddd;
	}
	.listMenu li{
		font-size: .37rem;
		margin: .5rem .4rem;
	}
	.is_bgColor{
		background: #eee;
	}
	.menuIsTop{
		top:-3rem;	
	}
	#home >>> .van-dialog__message {
		font-size: .35rem;
	}
	#home >>> .van-overlay {
		background-color: rgba(0,0,0,.48);
	}
	#home >>>.van-button--default {
		color:#000;
	}
	#home >>>.van-dialog__confirm, .van-dialog__confirm:active {
		color:red ;
	}
	#home >>> .van-dialog__footer {
		font-weight: bold;
	}
	.overHidden{
		overflow: hidden;
	}
</style>


